          <el-button type="primary" @click="adminPanelVisible = false">ÂÖ≥ Èó≠</el-button>
        </span>
      </el-dialog>

      <el-dialog title="üì¢ ÂèëÂ∏ÉÂ§±Áâ©ÊãõÈ¢Ü" :visible.sync="lostFoundVisible" width="500px">
        <el-form :model="lostFoundForm" label-width="80px">
          <el-form-item label="Á±ªÂûã" required>
            <el-radio-group v-model="lostFoundForm.lostType">
              <el-radio label="lost" border>üòì Êàë‰∏¢‰∏úË•ø‰∫Ü</el-radio>
              <el-radio label="found" border>üòÉ ÊàëÊç°Âà∞‰∫Ü</el-radio>
            </el-radio-group>
          </el-form-item>
          <el-form-item label="Áâ©ÂìÅÂêçÁß∞" required>
            <el-input v-model="lostFoundForm.itemName" placeholder="‰æãÂ¶ÇÔºöÈªëËâ≤‰øùÊ∏©ÊùØ"></el-input>
          </el-form-item>
          <el-form-item label="ÊèèËø∞" required>
            <el-input type="textarea" v-model="lostFoundForm.description" placeholder="‰æãÂ¶ÇÔºöÂú®ÂåóÊìçÂú∫ÁúãÂè∞Êç°Âà∞ÔºåÊúâËìùËâ≤Ë¥¥Á∫∏..."></el-input>
          </el-form-item>
          <el-form-item label="ËÅîÁ≥ªÊñπÂºè" required>
            <el-input v-model="lostFoundForm.contact" placeholder="QQ / ÂæÆ‰ø° / ÁîµËØù"></el-input>
          </el-form-item>
          <el-form-item label="ÊÇ®ÁöÑÁß∞Âëº">
            <el-input v-model="lostFoundForm.visitorName" placeholder="ÈªòËÆ§‰∏∫‚ÄúÊ∏∏ÂÆ¢‚Äù"></el-input>
          </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
          <el-button @click="lostFoundVisible = false">Âèñ Ê∂à</el-button>
          <el-button type="primary" @click="submitLostFound">Âèë Â∏É</el-button>
        </span>
      </el-dialog>

    </el-container>
  </el-container>
