<script>
import CampusMap from './components/CampusMap.vue';
import announcementData from './data/announcements.js';
import axios from 'axios';

export default {
  name: 'App',
  components: {
    CampusMap
  },
  data() {
    return {

      // üî• Êñ∞Â¢ûÔºöÈ°∂ÈÉ®ËèúÂçïÁöÑÈ¢úËâ≤ÊéßÂà∂ÂèòÈáè
      topMenuTextColor: '#fff',       // ÈªòËÆ§ÁôΩËâ≤
      topMenuActiveTextColor: '#ffd04b', // ÈªòËÆ§ÈªÑËâ≤


      // üî• Êñ∞Â¢ûÔºö‰∏ªÈ¢òÁä∂ÊÄÅÂèòÈáè
      currentTheme: 'default',
      menuBgColor: '#f5f7fa',
      menuTextColor: '#333',
      menuActiveTextColor: '#409EFF',

      drawerVisible: false,
      drawerTitle: '',
      currentDrawer: '',

      appointmentVisible: false,
      collegeInfoVisible: false,

      announcementList: announcementData,

      layers: {
        canteen: false,
        scenery: false,
        toilet: false,
        building: true
      },

      busLines: {
        line1: false,
        line2: false,
        houde: false
      },

      // Âª∫ËÆÆÁõ∏ÂÖ≥
      suggestionVisible: false,
      suggestionForm: {
        visitorName: '',
        content: ''
      },

      // ÁÆ°ÁêÜÂëòÁõ∏ÂÖ≥
      adminLoginVisible: false,
      adminPanelVisible: false,
      activeAdminTab: 'suggestion',
      adminPassword: '',
      isAdminLoggedIn: false,
      suggestionList: [],
      adminLostFoundList: [],

      // Â§±Áâ©ÊãõÈ¢ÜÁõ∏ÂÖ≥
      lostFoundVisible: false,
      lostFoundForm: {
        itemName: '',
        description: '',
        contact: '',
        lostType: 'lost',
        visitorName: '',
        longitude: null,
        latitude: null
      },

      weatherText: ''
    };
  },
  mounted() {
    this.fetchWeather();
  },
  methods: {
    // 1. Ëé∑ÂèñÂ§©Ê∞î
    fetchWeather() {
      axios.get('https://wttr.in/Zhengzhou?format=j1&lang=zh-cn').then(res => {
        if (res.status === 200) {
          const data = res.data;
          let desc = data.current_condition[0].lang_zh ? data.current_condition[0].lang_zh[0].value : data.current_condition[0].weatherDesc[0].value;
          this.weatherText = `ÈÉëÂ∑ûÂ∏Ç: ${desc}, Ê∏©Â∫¶: ${data.current_condition[0].temp_C}¬∞C`;
        }
      }).catch(err => { console.error(err); this.weatherText = "ÈÉëÂ∑û: Â§©Ê∞îÊï∞ÊçÆÂä†ËΩΩ‰∏≠..."; });
    },

    // 2. È°∂ÈÉ®ËèúÂçïÂ§ÑÁêÜ
    handleTopMenu(key) {
      if (key === '1') this.appointmentVisible = true;
      else if (key === '6') this.openDrawer('Ê†°Âõ≠ÂÖ¨Âëä', 'notice');
      else if (key === '7') this.openDrawer('ÂÖ¨‰∫§/Ê†°ËΩ¶Êü•ËØ¢', 'bus');
      else if (key === '10') { this.suggestionForm = { visitorName: '', content: '' }; this.suggestionVisible = true; }
      else if (key === '9') this.collegeInfoVisible = true;
      else if (key === '8') { if (this.isAdminLoggedIn) this.openAdminPanel(); else { this.adminPassword = ''; this.adminLoginVisible = true; } }
      else if (key === '11') { if (this.$refs.campusMap) this.$refs.campusMap.activateTool('pick-location'); }
    },

    // 3. üî• ‰∏ªÈ¢òÂàáÊç¢ÈÄªËæë (Êñ∞ÂäüËÉΩ)

    switchTheme(theme) {
      this.currentTheme = theme;

      if (theme === 'default') {
        // 1. ÊÅ¢Â§ç‰æßËæπÊ†è (ÈªòËÆ§)
        this.menuBgColor = '#f5f7fa';
        this.menuTextColor = '#333';
        this.menuActiveTextColor = '#409EFF';

        // 2. ÊÅ¢Â§çÈ°∂ÈÉ® (ÈªòËÆ§ÁôΩÂ≠óÈªÑÈ´ò‰∫Æ)
        this.topMenuTextColor = '#fff';
        this.topMenuActiveTextColor = '#ffd04b';

        this.changeBaseMap('day');
        this.$message.success("Â∑≤ÂàáÊç¢Ëá≥ÔºöÈÉëÂ§ßÁ∫¢ (ÈªòËÆ§)");
      }
      else if (theme === 'theme-blue') {
        // 1. ËÆæÁΩÆ‰æßËæπÊ†è (ÁßëÊäÄËìù)
        this.menuBgColor = '#021132';
        this.menuTextColor = '#00e1ff';
        this.menuActiveTextColor = '#fff';

        // 2. üî• ËÆæÁΩÆÈ°∂ÈÉ® (ËçßÂÖâËìùÂ≠ó + ÁôΩËâ≤È´ò‰∫Æ) üî•
        this.topMenuTextColor = '#00e1ff';
        this.topMenuActiveTextColor = '#fff';

        this.changeBaseMap('night');
        this.$message.success("Â∑≤ÂàáÊç¢Ëá≥ÔºöÁßëÊäÄËìù (Â§ßÂ±èÊ®°Âºè)");
      }
      else if (theme === 'theme-dark') {
        // 1. ËÆæÁΩÆ‰æßËæπÊ†è (ÊöóÈªë)
        this.menuBgColor = '#2b2b2b';
        this.menuTextColor = '#ccc';
        this.menuActiveTextColor = '#ffd04b';

        // 2. ËÆæÁΩÆÈ°∂ÈÉ® (ÊöóÈªëÊ®°Âºè‰∏ãÈ°∂ÈÉ®‰∏ÄËà¨ËøòÊòØÊµÖËâ≤Â≠ó)
        this.topMenuTextColor = '#ccc';
        this.topMenuActiveTextColor = '#ffd04b';

        this.changeBaseMap('night');
        this.$message.success("Â∑≤ÂàáÊç¢Ëá≥ÔºöÊöóÈªëÊ®°Âºè");
      }
    },

    // 4. Â§±Áâ©ÊãõÈ¢ÜÈÄªËæë
    onLocationPicked(coords) { this.lostFoundForm = { itemName: '', description: '', contact: '', lostType: 'lost', visitorName: '', longitude: coords.lng, latitude: coords.lat }; this.lostFoundVisible = true; },
    submitLostFound() { if (!this.lostFoundForm.itemName || !this.lostFoundForm.contact) { this.$message.warning("ËØ∑Â°´ÂÜôÁâ©ÂìÅÂêçÁß∞ÂíåËÅîÁ≥ªÊñπÂºè"); return; } this.$axios.post('/lost/add', this.lostFoundForm).then(() => { this.$message.success("ÂèëÂ∏ÉÊàêÂäüÔºÅ"); this.lostFoundVisible = false; if (this.$refs.campusMap) this.$refs.campusMap.loadLostFoundData(); }).catch(err => { console.error(err); this.$message.error("ÂèëÂ∏ÉÂ§±Ë¥•"); }); },
    deleteLostItem(id) { this.$confirm('Á°ÆÂÆöË¶ÅÂà†Èô§ËøôÊù°Â§±Áâ©ÊãõÈ¢Ü‰ø°ÊÅØÂêóÔºü', 'ÊèêÁ§∫', { type: 'warning' }).then(() => { this.$axios.post(`/lost/delete?id=${id}`).then(() => { this.$message.success("Âà†Èô§ÊàêÂäü"); this.openAdminPanel(); if (this.$refs.campusMap) this.$refs.campusMap.loadLostFoundData(); }); }); },

    // 5. Âª∫ËÆÆ‰∏éÁÆ°ÁêÜÂëòÈÄªËæë
    submitSuggestion() { if (!this.suggestionForm.content) { this.$message.warning("ËØ∑Â°´ÂÜôÂª∫ËÆÆÂÜÖÂÆπ"); return; } const payload = { visitorName: this.suggestionForm.visitorName || 'Ê∏∏ÂÆ¢', content: this.suggestionForm.content }; this.$axios.post('/suggestion/add', payload).then(() => { this.$message.success("Êèê‰∫§ÊàêÂäüÔºÅ"); this.suggestionVisible = false; }).catch(err => { console.error(err); this.$message.error("Êèê‰∫§Â§±Ë¥•"); }); },
    handleAdminLogin() { if (this.adminPassword === '123456') { this.$message.success("ÁÆ°ÁêÜÂëòÁôªÂΩïÊàêÂäü"); this.isAdminLoggedIn = true; this.adminLoginVisible = false; this.openAdminPanel(); } else { this.$message.error("ÂØÜÁ†ÅÈîôËØØ"); } },
    openAdminPanel() { this.$axios.get('/suggestion/list').then(res => { this.suggestionList = res.data; }); this.$axios.get('/lost/list').then(res => { this.adminLostFoundList = res.data.features || []; }); this.adminPanelVisible = true; },
    logoutAdmin() { this.isAdminLoggedIn = false; this.adminPanelVisible = false; this.$message.info("Â∑≤ÈÄÄÂá∫ÁÆ°ÁêÜÂëòÊ®°Âºè"); },

    // 6. Âú∞Âõæ‰∫§‰∫íÈÄªËæë
    openDrawer(title, type) { this.drawerTitle = title; this.currentDrawer = type; this.drawerVisible = true; },
    toggleLayer(layerName) { if (this.$refs.campusMap) this.$refs.campusMap.updateLayer(layerName, this.layers[layerName]); },
    changeBaseMap(type) { if (this.$refs.campusMap) this.$refs.campusMap.switchBaseMap(type); },
    toggleBus() { const targets = []; if (this.busLines.line1) targets.push("‰∏ÄÂè∑"); if (this.busLines.line2) targets.push("‰∫åÂè∑"); if (this.busLines.houde) targets.push("ÂéöÂæ∑"); if (this.$refs.campusMap) this.$refs.campusMap.updateBusLayer(targets); },
    resetMap() { if (this.$refs.campusMap) this.$refs.campusMap.resetMap(); else this.$message.info('Âú∞ÂõæÂ∑≤Â§ç‰Ωç'); },

    // 7. Â∑•ÂÖ∑ÊøÄÊ¥ª (Âê´Êó∂ÂÖâÊú∫)
    activateTool(toolName) {
      if (toolName === 'timemachine') {
        if (this.$refs.campusMap) this.$refs.campusMap.activateTimeMachine();
      } else {
        this.$message.info(`ÊøÄÊ¥ªÂ∑•ÂÖ∑: ${toolName}`);
        if (this.$refs.campusMap) this.$refs.campusMap.activateTool(toolName);
      }
    }
  },

    handleTopMenu(key) {
      if (key === '1') {
        this.appointmentVisible = true;
      } else if (key === '6') {
        this.openDrawer('Ê†°Âõ≠ÂÖ¨Âëä', 'notice');
      } else if (key === '7') {
        this.openDrawer('ÂÖ¨‰∫§/Ê†°ËΩ¶Êü•ËØ¢', 'bus');
      } else if (key === '10') {
        this.suggestionForm = { visitorName: '', content: '' };
        this.suggestionVisible = true;
      } else if (key === '9') {
        this.collegeInfoVisible = true;
      } else if (key === '8') {
        if (this.isAdminLoggedIn) {
          this.openAdminPanel();
        } else {
          this.adminPassword = '';
          this.adminLoginVisible = true;
        }
      } else if (key === '11') {
        // ÂºÄÂêØÂ§±Áâ©ÊãõÈ¢ÜÂèëÂ∏ÉÊ®°Âºè
        if (this.$refs.campusMap) {
          this.$refs.campusMap.activateTool('pick-location');
        }
      }
    },

    onLocationPicked(coords) {
      this.lostFoundForm = {
        itemName: '',
        description: '',
        contact: '',
        lostType: 'lost',
        visitorName: '',
        longitude: coords.lng,
        latitude: coords.lat
      };
      this.lostFoundVisible = true;
    },

    submitLostFound() {
      if (!this.lostFoundForm.itemName || !this.lostFoundForm.contact) {
        this.$message.warning("ËØ∑Â°´ÂÜôÁâ©ÂìÅÂêçÁß∞ÂíåËÅîÁ≥ªÊñπÂºè");
        return;
      }
      this.$axios.post('/lost/add', this.lostFoundForm)
          .then(() => {
            this.$message.success("ÂèëÂ∏ÉÊàêÂäüÔºÅ");
            this.lostFoundVisible = false;
            if (this.$refs.campusMap) {
              this.$refs.campusMap.loadLostFoundData();
            }
          })
          .catch(err => {
            console.error(err);
            this.$message.error("ÂèëÂ∏ÉÂ§±Ë¥•ÔºåËØ∑ÈáçËØï");
          });
    },

    submitSuggestion() {
      if (!this.suggestionForm.content) {
        this.$message.warning("ËØ∑Â°´ÂÜôÂª∫ËÆÆÂÜÖÂÆπ");
        return;
      }
      const payload = {
        visitorName: this.suggestionForm.visitorName || 'Ê∏∏ÂÆ¢',
        content: this.suggestionForm.content
      };
      this.$axios.post('/suggestion/add', payload)
          .then(() => {
            this.$message.success("Êèê‰∫§ÊàêÂäüÔºåÊÑüË∞¢ÊÇ®ÁöÑÂÆùË¥µÂª∫ËÆÆÔºÅ");
            this.suggestionVisible = false;
          })
          .catch(err => {
            console.error(err);
            this.$message.error("Êèê‰∫§Â§±Ë¥•ÔºåËØ∑Á®çÂêéÈáçËØï");
          });
    },

    handleAdminLogin() {
      if (this.adminPassword === '123456') {
        this.$message.success("ÁÆ°ÁêÜÂëòÁôªÂΩïÊàêÂäü");
        this.isAdminLoggedIn = true;
        this.adminLoginVisible = false;
        this.openAdminPanel();
      } else {
        this.$message.error("ÂØÜÁ†ÅÈîôËØØ");
      }
    },

    openAdminPanel() {
      // Ëé∑ÂèñÂª∫ËÆÆÂàóË°®
      this.$axios.get('/suggestion/list').then(res => {
        this.suggestionList = res.data;
      });
      // Ëé∑ÂèñÂ§±Áâ©ÂàóË°®
      this.$axios.get('/lost/list').then(res => {
        this.adminLostFoundList = res.data.features || [];
      });
      this.adminPanelVisible = true;
    },

    deleteLostItem(id) {
      this.$confirm('Á°ÆÂÆöË¶ÅÂà†Èô§ËøôÊù°Â§±Áâ©ÊãõÈ¢Ü‰ø°ÊÅØÂêóÔºüËøôÂ∞ÜÂêåÊó∂ÁßªÈô§Âú∞Âõæ‰∏äÁöÑÊ†áËÆ∞ÁÇπ„ÄÇ', 'ÊèêÁ§∫', {
        confirmButtonText: 'Á°ÆÂÆö',
        cancelButtonText: 'ÂèñÊ∂à',
        type: 'warning'
      }).then(() => {
        this.$axios.post(`/lost/delete?id=${id}`)
            .then(() => {
              this.$message.success("Âà†Èô§ÊàêÂäü");
              this.openAdminPanel(); // Âà∑Êñ∞ÂàóË°®
              if (this.$refs.campusMap) this.$refs.campusMap.loadLostFoundData(); // Âà∑Êñ∞Âú∞Âõæ
            })
            .catch(err => {
              console.error(err);
              this.$message.error("Âà†Èô§Â§±Ë¥•");
            });
      }).catch(() => {});
    },

    logoutAdmin() {
      this.isAdminLoggedIn = false;
      this.adminPanelVisible = false;
      this.$message.info("Â∑≤ÈÄÄÂá∫ÁÆ°ÁêÜÂëòÊ®°Âºè");
    },

    openDrawer(title, type) {
      this.drawerTitle = title;
      this.currentDrawer = type;
      this.drawerVisible = true;
    },
    toggleLayer(layerName) {
      const isChecked = this.layers[layerName];
      if (this.$refs.campusMap) {
        this.$refs.campusMap.updateLayer(layerName, isChecked);
      }
    },
    changeBaseMap(type) {
      if (this.$refs.campusMap) {
        this.$refs.campusMap.switchBaseMap(type);
      }
    },

    // üî• Â∑•ÂÖ∑‰∏éÊó∂ÂÖâÊú∫ÊøÄÊ¥ªÈÄªËæë
    activateTool(toolName) {
      if (toolName === 'timemachine') {
        if (this.$refs.campusMap) this.$refs.campusMap.activateTimeMachine();
      } else {
        this.$message.info(`ÊøÄÊ¥ªÂ∑•ÂÖ∑: ${toolName}`);
        if (this.$refs.campusMap) {
          this.$refs.campusMap.activateTool(toolName);
        }
      }
    },

    resetMap() {
      if (this.$refs.campusMap) {
        this.$refs.campusMap.resetMap();
      } else {
        this.$message.info('Âú∞ÂõæÂ∑≤Â§ç‰Ωç');
      }
    },
    toggleBus() {
      const targets = [];
      if (this.busLines.line1) targets.push("‰∏ÄÂè∑");
      if (this.busLines.line2) targets.push("‰∫åÂè∑");
      if (this.busLines.houde) targets.push("ÂéöÂæ∑");
      if (this.$refs.campusMap) {
        this.$refs.campusMap.updateBusLayer(targets);
      }
    }
};
</script>

